import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { employeeActions } from '../../../core/resource/actions';
import { createEmployeeSchema } from '../../../utils/ValidationSchema';
import { validateSchema } from '../../../utils/ValidationHelper';
import Button from '../../commanComponent/Button'
import { CustomButton, CustomError } from '../../globalComponents';
import { createShortName, dateFormate, multiSelectIDs } from '../../../utils/common'
import { useCustomTranslation } from '../../../translation'
import CustomSelect from '../../commanComponent/CustomSelect'
import moment from 'moment';
import { CustomMultiSelect, Label } from '../../commanComponent'

const CreateUpdateEmployee = (props) => {
    const { openModel,
        handleOpenClose,
        createEmployee,
        allDepartments,
        allDesignations,
        allEmployeeData,
        allBranches,
        getSubDepartmentByDepartment,
        subDepartmentList,
        selectedEmpData,
        allSkillListData,
        allProjectListData,
        filteredDropdownData
    } = props
    const { t } = useCustomTranslation()
    const [errors, setErrors] = useState({});
    const [createEmployeeData, setCreateEmployeeData] = useState(
        {
            "branch": "",
            "emp_id": "",
            "name": "",
            "mobile": "",
            "email": "",
            "working_from": null,
            "designation_id": "",
            "department_id": "",
            "reported_to": null,
            "joining_date": "",
            "selectedSubDepartment": null,
            "skills": null,
            "project": null,
        }
    );

    const handleFormData = async (e) => {
        errors && setErrors({});
        const { name, value } = e.target
        setCreateEmployeeData({ ...createEmployeeData, [name]: value })
    }

    const onSubmitForm = async () => {
        const validationResponse = await validateSchema(createEmployeeSchema, createEmployeeData);
        const skill =multiSelectIDs(createEmployeeData?.skills)
        const project =multiSelectIDs(createEmployeeData?.project)
        if (validationResponse.isValid) {
            setErrors({});
            const params = {
                branch_id: createEmployeeData?.branch?._id,
                emp_id: createEmployeeData?.emp_id,
                name: createEmployeeData?.name,
                mobile: createEmployeeData?.mobile,
                email: createEmployeeData?.email,
                working_from: createEmployeeData?.working_from?.value,
                reported_to: createEmployeeData?.reported_to?.value,
                joining_date: createEmployeeData?.joining_date,
                // location: createEmployeeData?.location?._id || '',
                department_id: createEmployeeData?.department_id?.value,
                designation_id: createEmployeeData?.designation_id?.value,
                sub_department_id: createEmployeeData?.selectedSubDepartment?.value,
                skill: skill,
                project:project

            }
            createEmployee(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e.target ? e.target : { value: e };
        if (fieldName === "department_id") {
            getSubDepartmentByDepartment(value?.value)
            setCreateEmployeeData(prevState => ({
                ...prevState,
                ["selectedSubDepartment"]: null,
            }));
        }
        setCreateEmployeeData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: value,
            };
            if (name === 'name') {
                updatedState.short_name = createShortName(value);
            }
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };
    useEffect(() => {
        if (selectedEmpData) {
            setCreateEmployeeData({
                branch: {label:selectedEmpData?.branch_name,value:selectedEmpData?.branch_id},
                emp_id: selectedEmpData?.emp_id,
                name: selectedEmpData?.name,
                mobile: selectedEmpData?.mobile,
                email: selectedEmpData?.email,
                working_from:{label:selectedEmpData?.working_from_name,value:selectedEmpData?.working_from},
                reported_to: {label:selectedEmpData?.reported_to_name,value:selectedEmpData?.reported_to_id},
                joining_date: selectedEmpData?.joining_date,
                department_id: {label:selectedEmpData?.department_name,value:selectedEmpData?.department_id},
                designation_id: {label:selectedEmpData?.designation_name,value:selectedEmpData?.designation_id},
                selectedSubDepartment:  {label:selectedEmpData?.sub_department_name,value:selectedEmpData?.sub_department_id},
                project: selectedEmpData?.project,
                skills: selectedEmpData?.skill,

            })
        }

    }, [selectedEmpData])
    return (
        <>

            <div className={`offcanvas offcanvas-end creatNewTicketOff ${openModel ? 'show' : ''}`} tabIndex={-1}
                style={{ visibility: openModel ? 'visible' : 'hidden' }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedEmpData ? t("headings.updateNewEmployee") : t("headings.createNewEmployee")}</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>
                <div className="offcanvas-body pt-0 mt-3">
                    <div className="formPanel">

                        <div className="row">
                            <div className="col-md-12">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.branchName")} required={true} />
                                    <CustomSelect
                                        data={allBranches}
                                        placeholder={t("placeholders.selectBranchName")}
                                        value={createEmployeeData?.branch || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "branch")}
                                        name="branch"
                                    />
                                    {errors?.branch && (
                                        <CustomError text={errors.branch} />
                                    )}
                                </div>
                            </div>
                            <label style={{ fontSize: "15px", fontWeight: "bold" }}><u>Basic Information</u></label>
                            <div className="col-md-12">
                                <div className="form-group mb-2">
                                    <label for="" className="text-primary">Employee ID</label>
                                    <input
                                        type="text"
                                        value={createEmployeeData?.emp_id}
                                        onChange={(e) => handleFormData(e)}
                                        name="emp_id"
                                        id=""
                                        className="form-control"
                                        placeholder="Enter Employee ID"
                                    />
                                    {errors.emp_id && <CustomError
                                        text={errors.emp_id}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="form-group mb-2">
                                    <label for="" className="text-primary">Name</label>
                                    <input
                                        type="text"
                                        value={createEmployeeData?.name}
                                        onChange={(e) => handleFormData(e)}
                                        name="name"
                                        id=""
                                        className="form-control"
                                        placeholder="Enter Name" />
                                    {errors.name && <CustomError
                                        text={errors.name}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <label for="" className="text-primary">Mobile No</label>
                                    <input
                                        type="text"
                                        value={createEmployeeData?.mobile}
                                        onChange={(e) => handleFormData(e)}
                                        name="mobile"
                                        id=""
                                        className="form-control"
                                        placeholder="Enter Mobile No." />
                                    {errors.mobile && <CustomError
                                        text={errors.mobile}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <label for="" className="text-primary">Official Email ID</label>
                                    <input
                                        type="text"
                                        value={createEmployeeData?.email}
                                        onChange={(e) => handleFormData(e)}
                                        name="email"
                                        id=""
                                        className="form-control"
                                        placeholder="Enter Email" />
                                    {errors.email && <CustomError
                                        text={errors.email}
                                    />}
                                </div>
                            </div>
                            <label style={{ fontSize: "15px", fontWeight: "bold" }}><u>Work Information</u></label>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <label for="" className="text-primary">Department</label>
                                    <CustomSelect
                                        data={allDepartments}
                                        placeholder={t("placeholders.selectDepartment")}
                                        value={createEmployeeData?.department_id || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "department_id")}
                                        name="department_id"
                                    />
                                    {errors?.department_id && (
                                        <CustomError text={errors.department_id} />
                                    )}
                                </div>
                            </div>

                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <label htmlFor="" className="text-primary">Sub Department/Function</label>
                                    <CustomSelect
                                        data={subDepartmentList}
                                        placeholder={t("placeholders.selectedSubDepartment")}
                                        value={createEmployeeData?.selectedSubDepartment || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "selectedSubDepartment")}
                                        name="selectedSubDepartment"
                                    />
                                    {errors?.selectedSubDepartment && (
                                        <CustomError text={errors.selectedSubDepartment} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <label htmlFor="" className="text-primary">Designation</label>
                                    <CustomSelect
                                        data={allDesignations}
                                        placeholder={t("placeholders.selectDesignation")}
                                        value={createEmployeeData?.designation_id || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "designation_id")}
                                        name="designation_id"
                                    />
                                    {errors?.designation_id && (
                                        <CustomError text={errors.designation_id} />
                                    )}
                                </div>
                            </div>

                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <label htmlFor="" className="text-primary">Skills</label>
                                    <CustomMultiSelect
                                        optionsData={allSkillListData}
                                        placeholder="Select Skills"
                                        values={createEmployeeData.skills}
                                        onChange={(data) => handleFromChange(data, "skills")}
                                    />
                                    {errors?.skills && (
                                        <CustomError text={errors.skills} />
                                    )}
                                </div>

                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <label htmlFor="" className="text-primary">Project</label>
                                    <CustomMultiSelect
                                        optionsData={allProjectListData}
                                        placeholder="Select Project"
                                        values={createEmployeeData.project}
                                        onChange={(data) => handleFromChange(data, "project")}
                                    />
                                    {errors?.project && (
                                        <CustomError text={errors.project} />
                                    )}
                                </div>

                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <label for="" className="text-primary">Joining Date</label>
                                    <input
                                        type="date"
                                        value={dateFormate(createEmployeeData?.joining_date,"YYYY-MM-DD")}
                                        onChange={(e) => handleFormData(e)}
                                        name="joining_date"
                                        className="form-control"
                                        placeholder="Enter Joining Date" />
                                    {errors.joining_date && <CustomError
                                        text={errors.joining_date}
                                    />}
                                </div>
                            </div>

                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <label for="" className="text-primary">Working From</label>
                                    <CustomSelect
                                        data={filteredDropdownData?.working_from}
                                        // isMulti={true}
                                        placeholder={"Working From"}
                                        value={createEmployeeData?.working_from || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "working_from")}
                                        name="working_from"
                                    />
                                    {errors.working_from && <CustomError
                                        text={errors.working_from}
                                    />}
                                </div>
                            </div>
                            <label style={{ fontSize: "15px", fontWeight: "bold" }}><u>Hierarchy Information</u></label>
                            <div className="col-md-12">
                                <div className="form-group mb-2">
                                    <label for="" className="text-primary">Reporting Manager</label>
                                    <CustomSelect
                                        data={allEmployeeData}
                                        // isMulti={true}
                                        placeholder={t("placeholders.selectReportedTo")}
                                        value={createEmployeeData?.reported_to || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "reported_to")}
                                        name="reported_to"
                                    />
                                    {errors?.reported_to && (
                                        <CustomError text={errors.reported_to} />
                                    )}
                                </div>
                            </div>

                        </div>

                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedEmpData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => onSubmitForm()}
                        />
                    </div>
                </div>

            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>

    )
}

export default CreateUpdateEmployee
