import React, { useEffect, useState } from 'react'
import { EmployeeBanner, EmployeeTable } from '.'
import { useDispatch, useSelector } from 'react-redux'
import CreateUpdateEmployee from './CreateUpdateEmployee'
import EmployeeDetail from './EmployeeDetail'
import { pagination } from '../../../utils/Constants.config'
import { departmentAction, designationAction, branchActions, employeeActions, authActions, dropdownMasterActions } from '../../../core/resource/actions'
import { useNavigate } from 'react-router-dom'
import ViewEmpDetail from './ViewEmpDetail'
import ChangePassword from './ChangePassword'
import { set } from 'lodash'
import UpdateActiveInactiveModal from './UpdateActiveInactiveModal'
import { setFilterDropdownData } from '../../../utils/common'

const Employee = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const {
        getAllEmployeePending,
        getAllEmployeeRes,
        getAllEmployeeError,

        createEmployeePending,
        createEmployeeRes,
        createEmployeeError,

        getAllEmployeeListPending,
        getAllEmployeeListRes,
        getAllEmployeeListError,

        updateEmployeeDetailsRes,
        updateEmployeeDetailsError,
        updateEmployeeDetailsPending,


    } = useSelector(state => state.employee)
    const {
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending,

        getSubDepartmentByDepartmentRes,
        getSubDepartmentByDepartmentError,
        getSubDepartmentByDepartmentPending,

        getAllSkillListPending,
        getAllSkillListRes,
        getAllSkillListError,

        getAllProjectListPending,
        getAllProjectListRes,
        getAllProjectListError,

    } = useSelector(state => state.department)
    const {
        getDesignationsByDepartmentRes,
        getDesignationsByDepartmentPending,
        getDesignationsByDepartmentError
    } = useSelector(state => state.designation)
    const {
        getAllBranchesRes,
        getAllBranchesPending,
        getAllBranchesError
    } = useSelector(state => state.branch)

    const {
        forgotPasswordForAdminRes,
        forgotPasswordForAdminPending,
        forgotPasswordForAdminError
    } = useSelector(state => state.auth)

        const {
            getFilteredDropdownDataError,
            getFilteredDropdownDataRes,
            getFilteredDropdownDataPending
        } = useSelector(state => state.dropdownMaster)

    const [currentPage, setCurrentPage] = useState(1);
    const [activeTabEmployeesData, setActiveTabEmployeesData] = useState('')
    const [isShowCreateUpdateEmployeeModal, setIsShowCreateUpdateEmployeeModal] = useState(false)
    const [activeTabType, setActiveTabType] = useState('')
    const [isShowEmpDetail, setIsShowEmpDetail] = useState(false)
    const [allDepartments, setAllDepartments] = useState([])
    const [allBranches, setAllBranches] = useState([])
    const [allDesignations, setAllDesignations] = useState([])
    const [allEmployeeData, setAllEmployeeData] = useState([])
    const [allEmployeeListData, setAllEmployeeListData] = useState([])
    const [empOnboardingDetail, setEmpOnboardingDetail] = useState(false)
    const [selectedEmpData, setSelectedEmpDetail] = useState('')
    const [isShowChangePassword, setIsShowChangePassword] = useState(false)
    const [accountStatusModal, setAccountStatusModal] = useState(false)
    const [subDepartmentList, setSubDepartmentList] = useState('')
    const [allSkillListData, setAllSkillListData] = useState([])
    const [allProjectListData, setAllProjectListData] = useState([])
    const [filteredDropdownData, setFilteredDropdownData] = useState('')
    const [activeModal,setActiveModal]=useState('')

    useEffect(() => {
        getAllDepartments()
        getAllEmployees()
        getAllBranches()
        handleDesignationByDepartment()
        getAllSkillList()
        getAllProjectList()
        getDropdownFilteredData()
    }, [])

    const handleCreateUpdateEmployeeModal = (data) => {
        setActiveModal("createUpdate")
        if (data) {
            setSelectedEmpDetail(data)
        } else {
            setSelectedEmpDetail('')
        }
        setIsShowCreateUpdateEmployeeModal(!isShowCreateUpdateEmployeeModal)
    }

    const createUpdateEmployee = (params) => {
        if (selectedEmpData) {
            params["_id"] = selectedEmpData?._id
            dispatch(employeeActions.updateEmployeeDetailsRequest(params))
        } else {
            dispatch(employeeActions.createEmployeeRequest(params))
        }
    };

    /* Get All Branches */
    const getAllBranches = () => {
        const branchResponse = getAllBranchesRes?.data
        if (!branchResponse?.length) {
            dispatch(branchActions.getAllBranchesRequest());
        } else {
            setAllBranches(branchResponse)
        }
    }

    useEffect(() => {
        if (!getAllBranchesPending) {
            if (getAllBranchesError) {
                return;
            }
            if (getAllBranchesRes) {
                if (getAllBranchesRes?.code === 200) {
                    const branchResponse = getAllBranchesRes?.data
                    setAllBranches(branchResponse)
                }
            }
        }
    }, [
        getAllBranchesRes,
        getAllBranchesError,
        getAllBranchesPending
    ]);

    /* Get All Departments */
    const getAllDepartments = () => {
        const departmentResposnse = getDepartmentListRes?.data
        if (!departmentResposnse?.length) {
            dispatch(departmentAction.getDepartmentListRequest());
        } else {
            setAllDepartments(departmentResposnse)
        }
    }

    useEffect(() => {
        if (!getDepartmentListPending) {
            if (getDepartmentListError) {
                return;
            }
            if (getDepartmentListRes) {
                if (getDepartmentListRes?.code === 200) {
                    const departmentResponse = getDepartmentListRes?.data
                    setAllDepartments(departmentResponse)
                }
            }
        }
    }, [
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    ]);

    useEffect(() => {
        if (!createEmployeePending) {
            if (createEmployeeError) {
                dispatch(employeeActions.handleClearEmployeeStore('HANDLE_CLEAR_CREATE_EMPLOYEE_STORE'))
                return;
            }
            if (createEmployeeRes) {
                if (createEmployeeRes?.data?.code == 200) {
                    dispatch(employeeActions.handleClearEmployeeStore('HANDLE_CLEAR_CREATE_EMPLOYEE_STORE'))
                    getAllEmployeesList()
                }
            }
        };
    }, [
        createEmployeeRes,
        createEmployeeError,
        createEmployeePending,
    ]);



    const getAllEmployees = () => {
        dispatch(employeeActions.getAllEmployeeRequest());

    }

    useEffect(() => {
        if (!getAllEmployeePending) {
            if (getAllEmployeeError) {
                return;
            }
            if (getAllEmployeeRes) {
                if (getAllEmployeeRes?.code == 200) {
                    const employeeResponse = getAllEmployeeRes?.data
                    setAllEmployeeData(employeeResponse)
                }
            }
        }
    }, [
        getAllEmployeeRes,
        getAllEmployeeError,
        getAllEmployeePending
    ]);

    // View emp Detail
    const handleViewEmployeeDetail = (data) => {
        setIsShowEmpDetail(!isShowEmpDetail)
        setSelectedEmpDetail(data)
    }
    const toggleEmpOnboardingDetail = () => {
        setEmpOnboardingDetail(!empOnboardingDetail)
    }

    const handleDesignationByDepartment = () => {
        dispatch(designationAction.getDesignationsByDepartmentRequest());
    }

    useEffect(() => {
        if (!getDesignationsByDepartmentPending) {
            if (getDesignationsByDepartmentError) {
                return;
            }
            if (getDesignationsByDepartmentRes) {
                if (getDesignationsByDepartmentRes?.code === 200) {
                    const designationResponse = getDesignationsByDepartmentRes?.data
                    setAllDesignations(designationResponse)
                }
            }
        }
    }, [
        getDesignationsByDepartmentRes,
        getDesignationsByDepartmentError,
        getDesignationsByDepartmentPending
    ]);




    useEffect(() => {
        getAllEmployeesList()

    }, [currentPage, activeTabType])

    const getAllEmployeesList = () => {
        let params = {
            page: currentPage,
            limit: pagination.limit,
        }
        activeTabType && (params["status"] = activeTabType)
        dispatch(employeeActions.getAllEmployeeListRequest(params));
    }
    useEffect(() => {
        if (!getAllEmployeeListPending) {
            if (getAllEmployeeListError) {
                return;
            }
            if (getAllEmployeeListRes) {
                if (getAllEmployeeListRes?.code == 200) {

                    const employeeListResponse = getAllEmployeeListRes?.data
                    setAllEmployeeListData(employeeListResponse)
                }
            } else {
                setAllEmployeeListData(getAllEmployeeListRes?.data)
            }
        }
    }, [
        getAllEmployeeListRes,
        getAllEmployeeListError,
        getAllEmployeeListPending
    ]);

    const handleChangePasswordModal = () => {
        setIsShowChangePassword(!isShowChangePassword)
    }
    const toggleChangeAccountStatusModal = () => {
        setActiveModal("activeInactive")
        setAccountStatusModal(!accountStatusModal)
    }
    const handleUpdateStatus = (payload) => {
        
        if (selectedEmpData) {
            payload["_id"] = selectedEmpData?._id
        }
        dispatch(employeeActions.updateEmployeeDetailsRequest(payload))
    }

    useEffect(() => {
        if (!updateEmployeeDetailsPending) {
            if (updateEmployeeDetailsError) {
                dispatch(employeeActions.handleClearEmployeeStore("HANDLE_CLEAR_UPDATE_EMPLOYEE_DETAILS_STORE"))
                return;
            }
            if (updateEmployeeDetailsRes) {
                if (updateEmployeeDetailsRes?.code == 200) {
                    dispatch(employeeActions.handleClearEmployeeStore("HANDLE_CLEAR_UPDATE_EMPLOYEE_DETAILS_STORE"))
                    if("createUpdate"===activeModal){
                        handleCreateUpdateEmployeeModal()
                    }else{
                        toggleChangeAccountStatusModal()
                        handleViewEmployeeDetail()
                    }
                    getAllEmployeesList()
                }
            }
        }
    }, [
        updateEmployeeDetailsRes,
        updateEmployeeDetailsError,
        updateEmployeeDetailsPending
    ]);

    const getSubDepartmentByDepartment = (id) => {
        const payload = {
            department_id: id
        }
        dispatch(departmentAction.getSubDepartmentByDepartmentRequest(payload))
    }

    useEffect(() => {
        if (!getSubDepartmentByDepartmentPending) {
            if (getSubDepartmentByDepartmentError) {
                return;
            }
            if (getSubDepartmentByDepartmentRes) {
                if (getSubDepartmentByDepartmentRes?.code === 200) {
                    setSubDepartmentList(getSubDepartmentByDepartmentRes?.data)
                }
            }
        }
    }, [
        getSubDepartmentByDepartmentRes,
        getSubDepartmentByDepartmentError,
        getSubDepartmentByDepartmentPending
    ]);

    const handleChangePasswordForAdmin = (payload) => {
        if (selectedEmpData) {
            payload["_id"] = selectedEmpData?.id
        }
        dispatch(authActions.forgotPasswordForAdminRequest(payload))
    }

    useEffect(() => {
        if (!forgotPasswordForAdminPending) {
            if (forgotPasswordForAdminError) {
                dispatch(authActions.handleClearAuthStore("HANDLE_CLEAR_FORGOT_PASSWORD_FOR_ADMIN_STORE"))
                return;
            }
            if (forgotPasswordForAdminRes) {
                if (forgotPasswordForAdminRes?.code == 200) {
                    dispatch(authActions.handleClearAuthStore("HANDLE_CLEAR_FORGOT_PASSWORD_FOR_ADMIN_STORE"))
                    handleChangePasswordModal()
                    handleViewEmployeeDetail()
                    getAllEmployeesList()
                }
            }
        }
    }, [
        forgotPasswordForAdminRes,
        forgotPasswordForAdminError,
        forgotPasswordForAdminPending
    ]);


    /* Get all Skills List Data */
    const getAllSkillList = () => {
        dispatch(departmentAction.getAllSkillListRequest())
    }

    useEffect(() => {
        if (!getAllSkillListPending) {
            if (getAllSkillListError) {
                return;
            }
            if (getAllSkillListRes) {
                if (getAllSkillListRes?.code == 200) {
                    setAllSkillListData(getAllSkillListRes?.data)
                }
            }
        }
    }, [
        getAllSkillListPending,
        getAllSkillListRes,
        getAllSkillListError,
    ]);

    /* Get all Skills List Data */
    const getAllProjectList = () => {
        dispatch(departmentAction.getAllProjectListRequest())
    }

    useEffect(() => {
        if (!getAllProjectListPending) {
            if (getAllProjectListError) {
                return;
            }
            if (getAllProjectListRes) {
                if (getAllProjectListRes?.code == 200) {
                    setAllProjectListData(getAllProjectListRes?.data)
                }
            }
        }
    }, [
        getAllProjectListPending,
        getAllProjectListRes,
        getAllProjectListError,
    ]);

     /* Dropdown Master Filter */
        const getDropdownFilteredData = () => {
            const categories = ['working_from']
            let params = { category: categories }
            dispatch(dropdownMasterActions.getFilteredDropdownDataRequest(params))
    
        }
        useEffect(() => {
            if (!getFilteredDropdownDataPending) {
                if (getFilteredDropdownDataError) {
                    return;
                }
                if (getFilteredDropdownDataRes) {
                    if (getFilteredDropdownDataRes?.code === 200) {
                        const updateData = setFilterDropdownData(getFilteredDropdownDataRes?.data?.data)
                        setFilteredDropdownData(updateData);
                    }
                }
            }
        }, [
            getFilteredDropdownDataRes,
            getFilteredDropdownDataError,
            getFilteredDropdownDataPending
        ]);


    return (
        <>

            <div className="ticketScoreSection">
                <EmployeeBanner
                    handleCreateUpdateEmployeeModal={handleCreateUpdateEmployeeModal}
                />
                <EmployeeTable
                    activeTabEmployeesData={activeTabEmployeesData}
                    isLoading={getAllEmployeeListPending}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                    setActiveTabType={setActiveTabType}
                    handleViewEmployeeDetail={handleViewEmployeeDetail}
                    toggleEmpOnboardingDetail={toggleEmpOnboardingDetail}
                    handleCreateUpdateEmployeeModal={handleCreateUpdateEmployeeModal}
                    allEmployeeListData={allEmployeeListData}


                />
            </div>
            {isShowCreateUpdateEmployeeModal && <CreateUpdateEmployee
                openModel={isShowCreateUpdateEmployeeModal}
                handleOpenClose={handleCreateUpdateEmployeeModal}
                createEmployee={createUpdateEmployee}
                allDepartments={allDepartments}
                allDesignations={allDesignations}
                allEmployeeData={allEmployeeData}
                allBranches={allBranches}
                getSubDepartmentByDepartment={getSubDepartmentByDepartment}
                subDepartmentList={subDepartmentList}
                selectedEmpData={selectedEmpData}
                allSkillListData={allSkillListData}
                allProjectListData={allProjectListData}
                filteredDropdownData={filteredDropdownData}

            />}
            {empOnboardingDetail && <EmployeeDetail
                openModel={empOnboardingDetail}
                handleOpenClose={toggleEmpOnboardingDetail}
            />}

            {isShowEmpDetail && <ViewEmpDetail
                openModel={isShowEmpDetail}
                handleOpenClose={handleViewEmployeeDetail}
                selectedEmpData={selectedEmpData}
                handleChangePasswordModal={handleChangePasswordModal}
                toggleChangeAccountStatusModal={toggleChangeAccountStatusModal}
            />}

            {isShowChangePassword && <ChangePassword
                openModel={isShowChangePassword}
                handleOpenClose={handleChangePasswordModal}
                selectedEmpData={selectedEmpData}
                handleChangePasswordForAdmin={handleChangePasswordForAdmin}
            />}

            {accountStatusModal && <UpdateActiveInactiveModal
                openModel={accountStatusModal}
                handleOpenClose={toggleChangeAccountStatusModal}
                selectedEmpData={selectedEmpData}
                handleUpdateStatus={handleUpdateStatus}

            />}


        </>
    )
}

export default Employee
