import React, { useState } from 'react'
import { CustomButton, CustomError } from '../../globalComponents'
import { useCustomTranslation } from '../../../translation'
import { Input, Label } from '../../commanComponent'
import { validateSchema } from '../../../utils/ValidationHelper'
import {  changePasswordForAdminSchema, resetPasswordSchema } from '../../../utils/ValidationSchema'

const ChangePassword = (
    {
        openModel,
        btnLeftLoading = false,
        btnRightLoading = false,
        btnTextLeft,
        btnTextRight,
        handleOpenClose,
        handleChangePasswordForAdmin
    }
) => {
    const { t } = useCustomTranslation()
    const [errors, setErrors] = useState({})
    const [passwordVisible, setPasswordVisible] = useState({
        newPassword: false,
        confirmPassword: false
    });
    const [formData, setFromData] = useState({
        newPassword: '',
        confirmPassword: ''
    })

    const handleFormData = (e) => {
        const { value, name, } = e.target

        setFromData(prevState => ({
            ...prevState,
            [name]: value,
        }));

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    }

    const togglePasswordVisibility = (field) => {
        setPasswordVisible(prevState => ({
            ...prevState,
            [field]: !prevState[field]  
        }));
    };

    const SubmitResetPasswordForm=async()=>{
        const validationResponse = await validateSchema(changePasswordForAdminSchema, formData);
        if (validationResponse.isValid) {
            const {
                newPassword,
                confirmPassword
            }=formData
            const params ={
                new_password:newPassword,
                confirm_password:confirmPassword
            }
            handleChangePasswordForAdmin(params)
            
        }
        else{
            setErrors(validationResponse.errors);
        }
    }
    return (
        <>
            <div className="modal fade " id='' style={{ display: openModel ? "unset !important" : "none !important", opacity: openModel ? "unset" : "0" }}>
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content rounded-1 ">
                        <div className="modal-header ">
                            <h1 className="modal-title fs-5 fw-bold text-primary">{t("headings.changePassword")}</h1>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" onClick={() => handleOpenClose()}></button>
                        </div>
                        <div className="modal-body pt-0">
                            <form>
                               
                                <div className="form-group password">
                                    <Label
                                        htmlFor={"new-password"}
                                        className={"col-form-label text-primary fw-semibold  d-block fs-8"}
                                        text={t("labels.newPassword")}
                                        required={true}
                                        autocomplete={"on"}
                                        
                                    />
                                    <Input
                                        type={passwordVisible.newPassword ? "text" : "password"}
                                        id="new-password"
                                        value={formData?.newPassword}
                                        name={"newPassword"}
                                        onChange={(e) => handleFormData(e)}
                                        autocomplete={"on"}
                                        placeholder={t("placeholders.newPassword")}
                                    />
                                     {errors?.newPassword && (
                                        <CustomError text={errors.newPassword} />
                                    )}
                                   <span className='eyeIcon'> <i
                                        className={` fa-light fa ${passwordVisible.newPassword ? "fa-eye-slash" : "fa-eye"}`}
                                        aria-hidden="true"
                                        onClick={() => togglePasswordVisibility('newPassword')}
                                    ></i>
                                    </span>

                                </div>
                                <div className="form-group password">
                                    <Label
                                        htmlFor={"confirm-password"}
                                        className={"col-form-label text-primary fw-semibold  d-block fs-8"}
                                        text={"Confirm Password"}
                                        required={true}
                                    />
                                    <Input
                                        type={passwordVisible.confirmPassword ? "text" : "password"}
                                        id="confirm-password"
                                        value={formData?.confirmPassword}
                                        name='confirmPassword'
                                        onChange={(e) => handleFormData(e)}
                                        autocomplete={"on"}
                                        placeholder={t("placeholders.confirmPassword")}
                                    />
                                    {errors?.confirmPassword && (
                                        <CustomError text={errors.confirmPassword} />
                                    )}
                                     <span className='eyeIcon'> <i
                                        className={` fa-light fa ${passwordVisible.confirmPassword ? "fa-eye-slash" : "fa-eye"}`}
                                        aria-hidden="true"
                                        onClick={() => togglePasswordVisibility('confirmPassword')}
                                    ></i>
                                    </span>
                                </div>
                                <div className="my-3 d-flex justify-content-center gap-3">
                                <CustomButton
                                    classNames={"btn btn-danger"}
                                    isLoading={btnLeftLoading}
                                    text={btnTextLeft ? btnTextLeft : t("btns.cancel")}
                                    handleClick={() => handleOpenClose()}
                                />
                                <CustomButton
                                    classNames={"btn btn-success"}
                                    isLoading={btnRightLoading}
                                    text={btnTextRight ? btnTextRight : t("btns.confirm")}
                                    handleClick={() => SubmitResetPasswordForm(true)}
                                />
                            </div>
                            </form>

                           
                        </div>
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default ChangePassword
