import React, { useState } from 'react'
import { CustomSpinner } from '../../../globalComponents';
import moment from 'moment';
import BlankTableError from '../../../commanComponent/BlankTableError';

const EmployeeHistoryDetails = (props) => {
    const { employeeHistoryData, isLoading, empHistoryConstantData, activeTabType, onChangeActive, type = "admin" } = props
    const [isOpen, setIsOpen] = useState(false);
    const [openIds, setOpenIds] = useState({});
    const [selectedDetails, setSelectedDetails] = useState({});
    const [isShowFile, setIsShowFile] = useState(false);
    const [fileData, setFileData] = useState({});

    const getIcon = (category, type) => {
        let icon = '';
        const filteredCategory = empHistoryConstantData && empHistoryConstantData?.filter((item, key) => item?.value === category);
        if (filteredCategory?.length) {
            const filteredType = filteredCategory[0]?.sub_category.filter((item, key) => item?.value === type);
            if (filteredType?.length) {
                icon = filteredType[0]?.icon
            }
        }
        return icon
    }

    const getShowModelStatus = (category, type) => {
        let status = '';
        const filteredCategory = empHistoryConstantData && empHistoryConstantData?.filter((item, key) => item?.value === category);
        if (filteredCategory?.length) {
            const filteredType = filteredCategory[0]?.sub_category.filter((item, key) => item?.value === type);
            if (filteredType?.length) {
                status = filteredType[0]?.is_salary_model
            }
        }
        return status
    }

    const fileDataToView = (file_name, file_path) => {
        let mime = file_name.split(".");
        setFileData({
            filename: file_name,
            payload: file_path || '',
            mime: mime?.length ? mime[mime?.length - 1] : '',
        });
        setIsShowFile(true);
    }

    const isViewModalOpen = (status, item) => {
        setIsOpen(status);
        setSelectedDetails(item);
    };


    const toggleCollapse = (id) => {
        setOpenIds((prev) => ({ [id]: !prev[id] }));
    };

    const renderEmployeeHistoryData = () => {
        return (
            employeeHistoryData && employeeHistoryData.length > 0
                ? employeeHistoryData.map((item, index) => (
                    <React.Fragment key={index}>
                        {activeTabType === 'performance' ? (
                            <li className="select flex-wrap d-block active">
                                <div className='d-flex gap-3'>
                                    <figure className="m-0">
                                        <i className={`${getIcon(activeTabType, item?.type) || "bi-file-text"}`} aria-hidden="true"></i>
                                    </figure>
                                    <strong>
                                        {item?.description}
                                        {item?.months ?
                                            <b
                                                style={{ color: 'blue', cursor: 'pointer' }}
                                                onClick={() => toggleCollapse(item?.financial_year_name)}
                                                role="button">
                                                {openIds[item?.financial_year_name] ? 'Hide' : 'View More'}
                                            </b>
                                            : <b>{moment(item?.created_at).format("DD-MM-YYYY")}</b>
                                        }
                                    </strong>
                                </div>
                                <div
                                    className={`collapse ${openIds[item?.financial_year_name] ? 'show' : ''}`}
                                    id={`id${item?.financial_year_name}`}
                                >
                                    <div className="card card-body mt-2" style={{ marginLeft: '62px' }}>
                                        {item?.months && item?.months.map((item1, index1) => (
                                            <li className="select active" key={index1}>
                                                <div className='d-flex gap-3'>
                                                    <figure className="m-0">
                                                        <i className={`${getIcon(activeTabType, item1?.type) || "bi bi-calendar-event"}`} aria-hidden="true" style={{ background: "#f28847" }}></i>
                                                    </figure>
                                                    <strong>
                                                        {item1?.description}
                                                        <b style={{ color: 'black' }}>{item1?.feedback && item1?.feedback !== 'undefined' ? item1?.feedback : ''}</b>
                                                    </strong>
                                                </div>
                                            </li>
                                        ))}
                                    </div>
                                </div>
                            </li>
                        ) : (
                            <li className="select active" key={index}>
                                <figure className="m-0">
                                    <i
                                        className={getIcon(activeTabType, item?.type) ? `${getIcon(activeTabType, item?.type)}` : "bi-file-text"} aria-hidden="true"></i>
                                </figure>
                                <strong>
                                    {item?.description} <b>{moment(item?.created_at).format("DD-MM-YYYY")}</b>
                                    {getShowModelStatus(activeTabType, item?.type) && item?.is_salary_actioned === '0' && type === 'admin' &&
                                        <b
                                            style={{ color: 'blue', cursor: 'pointer' }}
                                            onClick={e => isViewModalOpen(!isOpen, item)}
                                            role="button">
                                            Salary Impacted?
                                        </b>
                                    }
                                    {item?.file_name && type === 'admin' &&
                                        <b
                                            style={{ color: 'blue', cursor: 'pointer' }}
                                            onClick={e => fileDataToView(item?.file_name, item?.file_path)}
                                            role="button">
                                            View File
                                        </b>
                                    }
                                </strong>
                            </li>
                        )}
                    </React.Fragment>
                ))
                : (
                    <div className='d-flex justify-content-center'><BlankTableError /></div>
                )
        );
    };

    return (
        <div>
            <div className="detailsCandidate mt-3">
                <ul className="empHistoryDetailsList">

                    {true ? renderEmployeeHistoryData() : <CustomSpinner isShow={true} size="xl" />}

                </ul>
                {/* <FilePreviewModal
                isOpen={isShowFile}
                toggleModal={() => {
                    setIsShowFile(false);
                }}
                fileData={fileData}
            /> */}
            </div>
            {/* <SalaryChanges
            isOpen={isOpen}
            isViewModalOpen={isViewModalOpen}
            details={selectedDetails}
            onChangeActive={onChangeActive}
        /> */}
        </div>
    )
}

export default EmployeeHistoryDetails
