import React from 'react'

const EmpHistoryCard = (props) => {
    const {employeeData}=props
  return (
    <>
       
                <div className="row px-md-1">
                    <div className="col-md-12 mb-3 px-md-2">
                        <div className="cardBox mb-3 ">
                            <div className="row ">

                                <div className="cardHead col-md-10">
                                    <img
                                        src="/images/kpi-guidline.svg"
                                        alt=""
                                        width={40}
                                        className="me-2 fw-bold"
                                    />
                                    <h3 className='underlineHead ' style={{ fontSize: "18px" }}>
                                        {employeeData?.name}{" "}{`(${employeeData?.emp_id})`}
                                    </h3>
                                </div>
                                {/* <div className="profileInfoTxt col-md-10">
                                    
                                    <h3>{employeeData?.employee_name}</h3>
                                    <strong>{employeeData?.designation}</strong>
                                </div> */}
                                <div className=" col-md-2">
                                    <figure className="m-0 mt-2">
                                        <img
                                            src='/images/profile.png'
                                        // src={employeeData?.file_path && employeeData?.img_path ? employeeData?.file_path + employeeData?.img_path : "/avatars/6.jpg"} 
                                        height="100px" 
                                        alt="profile img"
                                        // style={{
                                        //     borderRadius: "10px",
                                        //     border: "2px solid #EEEEEE"
                                        // }}
                                        />
                                    </figure>
                                </div>
                            </div>
                            <div className='row mt-3 cardBoxD'>
                                {/* <!-- Left Column --> */}
                                <div class="col-md-6">
                                    <div class="mb-3 row">
                                        <div class="col-md-3">
                                            <strong>Work Mobile:</strong>
                                        </div>
                                        <div class="col-md-6">
                                            <span class="text-primary">{employeeData?.mobile}</span>
                                        </div>
                                    </div>

                                    <div class="mb-3 row mt-0">
                                        <div class="col-md-3">
                                            <strong>Work Email:</strong>
                                        </div>
                                        <div class="col-md-6">
                                            <span class="text-primary">{employeeData?.email}</span>
                                        </div>

                                    </div>
                                </div>

                                {/* <!-- Right Column --> */}
                                <div class="col-md-6">
                                    <div class="mb-3 row">
                                        <div class="col-md-3">
                                            <strong>Department:</strong>
                                        </div>
                                        <div class="col-md-6">
                                            <span
                                                class="text-primary"
                                            >
                                                {employeeData?.department}
                                            </span>
                                        </div>
                                    </div>
                                    <div class="mb-3 row">
                                        <div class="col-md-3">
                                            <strong>Designation:</strong>
                                        </div>
                                        <div class="col-md-6">
                                            <span
                                                class="text-primary"
                                            >
                                                {employeeData?.designation}
                                            </span>
                                        </div>
                                    </div>
                                    <div class="mb-3 row">
                                        <div class="col-md-3">
                                            <strong>Reported To:</strong>
                                        </div>
                                        <div class="col-md-6">
                                            <span
                                                class="text-primary"
                                            >
                                                {employeeData?.manager_name}
                                            </span>
                                        </div>
                                    </div>

                                </div>

                            </div>

                        </div>
                    </div>

                </div>
            
    </>
  )
}

export default EmpHistoryCard
