import React, { useState } from 'react'
import { Label } from '../../commanComponent'
import { dateFormate } from '../../../utils/common'
import { CustomButton } from '../../globalComponents'

const ViewEmpDetail = (props) => {
    const { openModel, handleOpenClose, selectedEmpData,handleChangePasswordModal,toggleChangeAccountStatusModal } = props


    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Employee Detail</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>
                <div className="offcanvas-body myRequest ">
                    <div className='emp-detail'>
                        {/* <h4 className='text-capitalize'>{selectedEmpData?.name}</h4> */}

                        <div className="row g-3 align-items-end ">
                            <div className="col-md-12">
                                <div className="form-group row mrfViewOnly">
                                    <label htmlFor="staticEmail" className="col-md-3 col-form-label" >Employee Id:</label>
                                    <div className="col-md">
                                        <strong className="form-control-plaintext" >{selectedEmpData?.emp_id || '-'}</strong>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="form-group row mrfViewOnly">
                                    <label htmlFor="staticEmail" className="col-md-3 col-form-label" >Job Location:</label>
                                    <div className="col-md">
                                        <strong className="form-control-plaintext" >{selectedEmpData?.branch_name || '-'}</strong>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="form-group row mrfViewOnly">
                                    <label htmlFor="staticEmail" className="col-md-3 col-form-label" >Department:</label>
                                    <div className="col-md">
                                        <strong className="form-control-plaintext" >{selectedEmpData?.department_name || "-"}</strong>
                                    </div>
                                </div>
                            </div>

                            <div className="col-md-12">
                                <div className="form-group row mrfViewOnly">
                                    <label htmlFor="staticEmail" className="col-md-3 col-form-label" >Reporting Manager:</label>
                                    <div className="col-md">
                                        <strong className="form-control-plaintext" >{selectedEmpData?.reported_to_name || "-"}</strong>
                                    </div>
                                </div>
                            </div>

                            <div className="col-md-12">
                                <div className="form-group row mrfViewOnly">
                                    <label htmlFor="staticEmail" className="col-md-3 col-form-label" >Created At:</label>
                                    <div className="col-md">
                                        <strong className="form-control-plaintext" >{selectedEmpData?.created_at || "-"}</strong>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="form-group row mrfViewOnly">
                                    <label htmlFor="staticEmail" className="col-md-3 col-form-label" >Joining Date:</label>
                                    <div className="col-md">
                                        <strong className="form-control-plaintext" >{dateFormate(selectedEmpData?.joining_date) || "-"}</strong>
                                    </div>
                                </div>
                            </div>
                            {/* <div className="col-md-12">
                                <div className="form-group row mrfViewOnly">
                                    <label htmlFor="staticEmail" className="col-md-3 col-form-label" >Password:</label>
                                    <div className="col-md">
                                        <strong className="form-control-plaintext" >{selectedEmpData?.password || "-"}</strong>
                                    </div>
                                </div>
                            </div> */}




                        </div>
                        <div className='mt-4 d-flex gap-3'>
                           {selectedEmpData?.account_status ==="inactive" ? <CustomButton
                                classNames={"btn btn-success gradientBg"}
                                handleClick={() => toggleChangeAccountStatusModal()}
                                text={"Active"}
                            />:
                            <CustomButton
                                classNames={"btn btn-danger gradientBg"}
                                handleClick={() => toggleChangeAccountStatusModal()}
                                text={"Inactive"}
                            />}

                            <CustomButton
                                classNames={"btn btn-oreang gradientBg"}
                                handleClick={() => handleChangePasswordModal()}
                                text={"Change Password"}
                            />
                        </div>


                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <button type="button" className="btn btn-secondary" onClick={() => handleOpenClose()}>Back</button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ViewEmpDetail
