import React, { useState } from 'react'
import { CustomButton, CustomError } from '../../globalComponents'
import { useCustomTranslation } from '../../../translation'
import { Input, Label } from '../../commanComponent'
import { validateSchema } from '../../../utils/ValidationHelper'
import { empActiveSchema, empInactiveSchema, resetPasswordSchema } from '../../../utils/ValidationSchema'

const UpdateActiveInactiveModal = (
    {
        openModel,
        btnRightLoading = false,
        btnTextRight,
        handleOpenClose,
        selectedEmpData,
        handleUpdateStatus
    }
) => {
    const { t } = useCustomTranslation()
    const [errors, setErrors] = useState({})

    const [formData, setFromData] = useState({
        leavingDate: '',
        reason: ''
    })

    const handleFormData = (e) => {
        const { value, name, } = e.target

        setFromData(prevState => ({
            ...prevState,
            [name]: value,
        }));

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    }



    const SubmitResetPasswordForm = async () => {
        let validationResponse=''
        if(selectedEmpData?.account_status==="active"){
             validationResponse = await validateSchema(empInactiveSchema, formData);
        }else{
            validationResponse = await validateSchema(empActiveSchema, formData); 
        }
        if (validationResponse.isValid) {
            const {
                leavingDate,
                reason
            } = formData
            const params = {
                reason: reason,
                account_status:selectedEmpData?.account_status==="active"?"inactive":"active"
            }
            selectedEmpData?.account_status==="active" &&( params["leaving_date"] =leavingDate)
            handleUpdateStatus(params)

        }
        else {
            setErrors(validationResponse.errors);
        }
    }
    return (
        <>
            <div className="modal fade " id='' style={{ display: openModel ? "unset !important" : "none !important", opacity: openModel ? "unset" : "0" }}>
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content rounded-1 ">
                        <div className="modal-header ">
                            <h1 className="modal-title fs-5 fw-bold text-primary">{selectedEmpData?.account_status ==="active"?"Inactive":"Active"} User</h1>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" onClick={() => handleOpenClose()}></button>
                        </div>
                        <div className="modal-body pt-0">
                            <form>

                                {selectedEmpData?.account_status ==="active" &&<div className="form-group password">
                                    <Label
                                        htmlFor={"new-password"}
                                        className={"col-form-label text-primary fw-semibold  d-block fs-8"}
                                        text={t("labels.leavingDate")}
                                        required={true}
                                        autocomplete={"on"}

                                    />
                                    <Input
                                        type={"date"}
                                        id="leaving-date"
                                        value={formData?.leavingDate}
                                        name={"leavingDate"}
                                        onChange={(e) => handleFormData(e)}
                                        autocomplete={"on"}
                                        placeholder={t("placeholders.leavingDate")}
                                    />
                                    {errors?.leavingDate && (
                                        <CustomError text={errors.leavingDate} />
                                    )}
                                    

                                </div>}
                                <div className="form-group password">
                                    <Label
                                        htmlFor={"reason"}
                                        className={"col-form-label text-primary fw-semibold  d-block fs-8"}
                                        text={"Reason"}
                                        required={true}
                                    />
                                    <Input
                                        type="text" 
                                        id="confirm-password"
                                        value={formData?.reason}
                                        name='reason'
                                        onChange={(e) => handleFormData(e)}
                                        autocomplete={"on"}
                                        placeholder={t("placeholders.reason")}
                                    />
                                    {errors?.reason && (
                                        <CustomError text={errors.reason} />
                                    )}
                                    
                                </div>
                               
                            </form>


                        </div>
                        <div className='modal-footer'>
                        {/* <div className=" d-flex justify-content-center gap-3"> */}
                                  
                                  <CustomButton
                                      classNames={"btn btn-success"}
                                      isLoading={btnRightLoading}
                                      text={btnTextRight ? btnTextRight : t("btns.confirm")}
                                      handleClick={() => SubmitResetPasswordForm(true)}
                                  />
                              </div>
                        {/* </div> */}
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default UpdateActiveInactiveModal
