const EmployeeHistory = () => {
    const [employeeData, setEmployeeData] = useState(
        {
            department: null,
            designation: "Trainee",
            email: "test2@mailinator.com",
            emp_id: "TD21",
            name: "Test A",
            file_path: "https://glueple.com:3001/employee_profile_picture/file.1722755533437.octet-stream",
            id: 3,
            manager_name: "Test A",
            mobile: "9632587410"
        })
    const [employeeHistoryData, setEmployeeHistoryData] = useState(performanceData)
    const [empHistoryConstantData, setEmpHistoryConstantData] = useState([])
    const [activeTabType, setActiveTabType] = useState('timeline')
    const tableTabsRequest = [
        { name: "Timeline", onClickFun: (id) => getEmployeeHistoryData(id), id: "timeLine" },
        { name: "Job", onClickFun: (id) => getEmployeeHistoryData(id), id: "job" },
        { name: "Performance", onClickFun: (id) => getEmployeeHistoryData(id), id: "performance" },
        { name: "Salary", onClickFun: (id) => getEmployeeHistoryData(id), id: "salary" },
    ];
    const getEmployeeHistoryData = (type) => {
        setActiveTabType(type)
    }
    const onChangeActive = (type) => {
        // setActive(type)
        // getEmploymentHistory(type)

    }
    return (
        <>
            <div className="cardBox myRequest mb-3 employeeHistory">
                <div className="cardHead">
                    <img src="/images/myrequest-icon.svg" alt="" width="35" />
                    <h3 className="ms-2">Employee History</h3>
                </div>
                <EmpHistoryCard
                    employeeData={employeeData}
                />
                <CustomTabs tabs={tableTabsRequest} type="employeeHistory" />
                <div className="tab-content mykpiMain">
                    <div className="tab-pane fade active show" id="timeLine">
                        <EmployeeHistoryDetails
                            employeeHistoryData={employeeHistoryData}
                            //    isLoading={loading}
                            empHistoryConstantData={empHistoryConstantData}
                            activeTabType={activeTabType}
                            onChangeActive={onChangeActive}                    
                        />
                    </div>
                    <div className="tab-pane fade" id="job">
                    <EmployeeHistoryDetails
                            employeeHistoryData={employeeHistoryData}
                            //    isLoading={loading}
                            empHistoryConstantData={empHistoryConstantData}
                            activeTabType={activeTabType}
                            onChangeActive={onChangeActive}
                        />
                    </div>
                    <div className="tab-pane fade" id="performance">
                        <EmployeeHistoryDetails
                            employeeHistoryData={employeeHistoryData}
                            //    isLoading={loading}
                            empHistoryConstantData={empHistoryConstantData}
                            activeTabType={activeTabType}
                            onChangeActive={onChangeActive}
                        />
                    </div>
                    <div className="tab-pane fade" id="salary">

                    <EmployeeHistoryDetails
                            employeeHistoryData={employeeHistoryData}
                            //    isLoading={loading}
                            empHistoryConstantData={empHistoryConstantData}
                            activeTabType={activeTabType}
                            onChangeActive={onChangeActive}
                        />
                    </div>

                </div>
            </div>

        </>
    )
}

export default EmployeeHistory
