import React, { useMemo, useState } from 'react'
import CustomTable from '../../reuseableComponents/customTable/CustomTable';
import { getBadgeColor } from '../../../utils/common';
import { Link } from 'react-router-dom';
import { Icon } from '../../commanComponent';

const EmployeeTable = (props) => {
    const {
        activeTabEmployeesData,
        isLoading,
        currentPage,
        setCurrentPage,
        setActiveTabType,
        handleViewEmployeeDetail,
        toggleEmpOnboardingDetail,
        handleCreateUpdateEmployeeModal,
        allEmployeeListData,
    } = props
    const [activeTab, setActiveTab] = useState("allEmployees");
    const handleActiveTab = (type) => {
        setActiveTab(type);
        setActiveTabType(type === "allEmployees" ? "" : type)
        setCurrentPage(1)
    }

    const allEmployeeField = [
        { key: 'sn', label: 'S.N' },
        { key: 'emp_id', label: 'Emp Id' },
        { key: 'name', label: 'Full Name' },
        { key: 'mobile', label: 'Mobile' },
        { key: 'email', label: 'Email' },
        { key: 'account_status', label: 'Account Status' },
        { key: 'action', label: 'Action' }
    ];

    const scopedSlotsAllEmployee = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        account_status: item => (
            <span
                className={`badge ${getBadgeColor(item.account_status)}`}
            >
                {item.account_status}
            </span>
        ),


        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span className="dropdown-item"
                            onClick={() => handleViewEmployeeDetail(item)}
                        >
                            View Detail</span>
                    </li>
                    <li>
                        <span className="dropdown-item"
                            onClick={() => handleCreateUpdateEmployeeModal(item)}
                        >Update</span>
                    </li>
                   
                    <li>
                        <Link to={`employee-history/${item?.id}`} className="dropdown-item"
                        >Employee History</Link>
                    </li>
                    <li>
                        <span className="dropdown-item"
                            onClick={() => toggleEmpOnboardingDetail(item?._id)}
                        >
                            View Onboarding Detail</span>
                    </li>



                </ul>
            </div>
        )
    }), [activeTabEmployeesData]);
    return (
        <>
            <div className="cardBox myRequest mb-3">
                <div className="cardHead">
                    <img src="images/myrequest-icon.svg" alt="" width="35" />
                    <h3 className="ms-2">Employees</h3>
                </div>
                <ul className="nav nav-tabs leaveTabs mb-3">
                    <li className="nav-item">
                        <button
                            className={`nav-link ${activeTab === "allEmployees" ? "active" : ""}`}
                            data-bs-toggle="tab"
                            data-bs-target="#allEmployees"
                            type="button"
                            onClick={() => handleActiveTab("allEmployees")}
                        >
                            All Employees
                        </button>
                    </li>
                    <li className="nav-item">
                        <button
                            className={`nav-link ${activeTab === "active" ? "active" : ""}`}
                            data-bs-toggle="tab"
                            data-bs-target="#active"
                            type="button"
                            onClick={() => handleActiveTab("active")}
                        >
                            Active Employees
                        </button>
                    </li>
                    <li className="nav-item">
                        <button
                            className={`nav-link ${activeTab === "inactive" ? "active" : ""}`}
                            data-bs-toggle="tab"
                            data-bs-target="#inactive"
                            type="button"
                            onClick={() => handleActiveTab("inactive")}
                        >
                            Inactive Employees
                        </button>
                    </li>
                </ul>
                <div className="tab-content">
                    <div className={`tab-pane fade ${activeTab === "allEmployees" ? "show active" : ""}`} id="allEmployees">
                        <div className="table-responsive tableDiv text-nowrap overflowTable">
                            <CustomTable
                                data={allEmployeeListData?.data}
                                fields={allEmployeeField}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={allEmployeeListData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsAllEmployee}
                            />

                        </div>
                    </div>
                    <div className={`tab-pane fade ${activeTab === "active" ? "show active" : ""}`} id="active">
                        <div className="table-responsive tableDiv text-nowrap">
                            <CustomTable
                                data={allEmployeeListData?.data}
                                fields={allEmployeeField}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={allEmployeeListData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsAllEmployee}
                            />
                        </div>
                    </div>
                    <div className={`tab-pane fade ${activeTab === "inactive" ? "show active" : ""}`} id="inactive">
                        <div className="table-responsive tableDiv text-nowrap">
                            <CustomTable
                                data={allEmployeeListData?.data}
                                fields={allEmployeeField}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={allEmployeeListData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsAllEmployee}
                            />
                        </div>
                    </div>


                </div>
            </div>
        </>
    )
}

export default EmployeeTable
